@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@800&family=Montserrat:wght@200;400;500;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
:root {
  --font-poppins: "Poppins", sans-serif;
  --font-heebo: "Heebo", sans-serif;
  --font-montserrat: "Montserrat", sans-serif;
}

:root {
  --Gray1: hsla(0, 0%, 20%, 1);
  --Gray2: hsla(0, 0%, 31%, 1);
  --Gray3: hsla(0, 0%, 51%, 1);
  --Gray4: hsla(0, 0%, 74%, 1);
  --Gray6: hsl(0, 0%, 95%);
  --LightBlue: hsla(202, 71%, 52%, 1);
  --LightGreen: hsla(145, 63%, 42%, 1);
  --LightRed: hsla(0, 79%, 63%, 1);
  --White: hsla(0, 0%, 100%, 1);
  --DarkBlue: hsla(248, 35%, 8%, 1);
}

h1,
h2,
h3,
p {
  color: var(--Gray1);
}

html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-poppins);
}

a,
a:hover,
a:visited {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1,
h2 {
  font-size: 1.5rem;
  font-weight: 500;
  line-height: 2.25rem;
  color: var(--Gray1);
}

@media (min-width: 768px) {
  h1 {
    font-size: 3rem;
    font-weight: 500;
    line-height: 4.5rem;
    color: var(--Gray1);
  }

  h2 {
    font-size: 2.25rem;
    font-weight: 500;
    line-height: 3.375rem;
    color: var(--Gray1);
  }
}
h3 {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 2.25rem;
  color: var(--Gray1);
}

q {
  font-family: Poppins;
  font-size: 24px;
  font-weight: 500;
  line-height: 36px;
  color: var(--Gray1);
}

.container {
  margin: 0.8125rem 1.0625rem 0 0.625rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .container {
    margin: 1rem 1.375rem 0 2rem;
  }
}
@media (min-width: 1024px) {
  .container {
    margin: 1.8125rem 2.4375rem 0 2.625rem;
  }
}
@media (min-width: 1440px) {
  .container {
    margin: 2.625rem 3.5rem 0 3.25rem;
  }
}

.extra-margin {
  margin-left: 1.875rem;
}
@media (min-width: 768px) {
  .extra-margin {
    margin-left: 5.125rem;
  }
}
@media (min-width: 1024px) {
  .extra-margin {
    margin-left: 10.25rem;
  }
}
@media (min-width: 1440px) {
  .extra-margin {
    margin-left: 14.0625rem;
  }
}

.hero {
  margin-top: 2.1875rem;
}
.hero__subtitle {
  max-width: 17rem;
}
@media (min-width: 768px) {
  .hero__subtitle {
    max-width: 25.75rem;
  }
}
@media (min-width: 1024px) {
