}
@media (min-width: 1024px) {
  .works__img {
    max-width: 33.375rem;
    max-height: 33.375rem;
    margin-bottom: 1.25rem;
  }
}

@media (min-width: 1024px) {
  .team {
    margin-left: 7.0625rem;
    margin-right: 6.875rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.team .text-wrapper {
  align-self: center;
}
.team__caption {
  margin-bottom: 0.4375rem;
}
.team__title {
  max-width: 17.5rem;
  margin-bottom: 0.875rem;
}
.team__paragraph {
  max-width: 18rem;
}
.team__gallery {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.556875rem;
  margin-top: 2.25rem;
  margin-bottom: 6.25rem;
}
.team__gallery img {
  border-radius: 1.5rem;
}
.team__gallery img:first-child {
  width: 9.0475rem;
  height: 9.0475rem;
}
@media (min-width: 1024px) {
  .team__gallery img:first-child {
    width: 16.25rem;
    height: 16.25rem;
  }
}
.team__gallery img:nth-child(2) {
  width: 11.135rem;
  height: 11.135rem;
}
@media (min-width: 1024px) {
  .team__gallery img:nth-child(2) {
    width: 18.1875rem;
    height: 18.75rem;
  }
}
.team__gallery img:nth-child(3) {
  width: 10.140625rem;
  height: 10.439375rem;
  grid-row: 1/span 2;
  align-self: center;
}
@media (min-width: 1024px) {
  .team__gallery img:nth-child(3) {
    width: 20rem;
    height: 20rem;
  }
}
@media (min-width: 768px) {
  .team .quote-wrapper {
    grid-column: 1/span 2;
  }
}
.team q {
  display: block;
  margin-bottom: 1.6875rem;
}
@media (min-width: 768px) {
  .team q {
    margin-bottom: 2.25rem;
    max-width: 40rem;
  }
}
.team__footer {
  display: flex;
  gap: 1.875rem;
  margin-bottom: 6.25rem;
}
.team__footer .profile-img {
  width: 5.125rem;
  height: 5.125rem;
  border-radius: 0.75rem;
}
.team__footer .text-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

li {
  list-style: none;
}

.header__logo {
  font-size: 1.5rem;
  font-weight: 800;
  font-family: var(--font-heebo);
  line-height: 2.203125rem;
}
@media (min-width: 768px) {
  .header__logo {
    font-size: 1.875rem;
    line-height: 2.68rem;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    font-size: 2.25rem;
    line-height: 3.305rem;
  }
}

.nav__link {
  color: var(--Gray1);
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 2.25rem;
}
@media (min-width: 1024px) {
  .nav__link {
    font-size: 1.5rem;
    line-height: 2.25rem;
  }
}

.hero__paragraph {
  font-size: 0.75rem;
  font-weight: 500;
  line-height: 1.125rem;
  color: var(--LightBlue);
}
@media (min-width: 768px) {
  .hero__paragraph {
    font-size: 0.9375rem;
    line-height: 1.4375rem;
  }
}
@media (min-width: 1024px) {
  .hero__paragraph {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}

.subtitle__paragraph {
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 1.125rem;
}
@media (min-width: 768px) {
  .subtitle__paragraph {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}

.form__label {
  font-size: 0.625rem;
  height: 1.3125rem;
  font-weight: 500;
  color: var(--Gray3);
}
@media (min-width: 768px) {
  .form__label {
    font-size: 0.875rem;
  }
}
.form__input::placeholder {
  color: var(--Gray4);
  font-size: 0.875rem;
  line-height: 1.3125rem;
  font-weight: 500;
}
@media (min-width: 768px) {
  .form__input::placeholder {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}

.btn {
  font-size: 0.875rem;
  line-height: 1.3125rem;
  font-weight: 500;
}
@media (min-width: 768px) {
  .btn {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}

.caption__type {
  font-size: 0.75rem;
  font-weight: 300;
  line-height: 1.125rem;
  color: var(--Gray3);
}
@media (min-width: 1024px) {
  .caption__type {
    font-size: 0.875rem;
    line-height: 1.3125rem;
  }
}

.caption__title {
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 1.6875rem;
}
@media (min-width: 1024px) {
  .caption__title {
    font-size: 1.5rem;
    line-height: 2.25rem;
  }
}

.works__gallery a {
  font-family: Poppins;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: var(--LightBlue);
}
@media (min-width: 1024px) {
  .works__gallery a {
    font-size: 24px;
    line-height: 36px;
  }
}

.team__caption {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  color: var(--LightRed);
}
.team__title {
  font-size: 36px;
  font-weight: 500;
  line-height: 54px;
}
.team__paragraph {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: var(--Gray2);
}
.team .quote-wrapper p {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  color: var(--Gray3);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}
.header__hamburger {
  transform: rotate(360deg);
  transition: all 0.3s ease-in-out;
  opacity: 1;
}
.header__hamburger--hide {
  transform: rotate(0deg);
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 768px) {
  .header__hamburger {
    position: absolute;
    visibility: hidden;
  }
}

.nav {
  overflow: hidden;
}
@media (min-width: 768px) {
  .nav {
    flex-grow: 0.3;
  }
}
.nav__menu {
  display: flex;
  position: fixed;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  justify-content: space-between;
  background-color: var(--Gray6);
  padding: 1.1875rem 0 7rem;
  align-items: center;
  transform: translateX(100%);
  transition: transform 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}
@media (min-width: 768px) {
  .nav__menu {
    width: auto;
    height: auto;
    position: static;
    padding: 0;
    transform: translateX(0);
    background-color: transparent;
    flex-direction: row;
    justify-content: space-between;
  }
}
.nav__menu--active {
  transform: translateX(0);
}
.nav__list {
  opacity: 0;
  transform: translateX(100px);
}
.nav__list:nth-child(2) {
  transition: all 0.6s ease-in-out;
  transition-delay: 0.8s;
}
.nav__list:nth-child(3) {
  transition: all 0.9s ease-in-out;
  transition-delay: 0.8s;
}
.nav__list:nth-child(4) {
  transition: all 1.2s ease-in-out;
  transition-delay: 0.8s;
}
.nav__list:nth-child(5) {
  transition: all 1.5s ease-in-out;
  transition-delay: 0.8s;
}
.nav__list:nth-child(6) {
  transition: all 1.8s ease-in-out;
  transition-delay: 0.8s;
}
@media (min-width: 768px) {
  .nav__list {
    opacity: 1;
    transition: none;
    transform: none;
  }
}
.nav__list--active {
  transform: translateX(0);
  opacity: 1;
}
.nav__link {
  display: block;
}
.nav__link::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  border-bottom: solid 2px var(--Gray1);
  position: relative;
  top: -0.1875rem;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform ease-in-out 0.3s;
}
.nav__link:hover::after {
  transform: scaleX(1);
  transform-origin: left;
  transition: transform ease-in-out 0.3s;
  transition-delay: 0.2s;
}
.nav__close {
  align-self: flex-end;
  margin-right: 1.0625rem;
}
@media (min-width: 768px) {
  .nav__close {
    display: none !important;
  }
}

.hero__image {
  margin-top: 0.9375rem;
  width: 100%;
  min-height: 9.0625rem;
  border-radius: 1.125rem;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.input-wrapper {
  display: flex;
  background-color: var(--Gray6);
  border-radius: 0.75rem;
  align-items: center;
  height: 2.9375rem;
  padding: 0.25rem;
}
@media (min-width: 768px) {
  .input-wrapper {
    height: 3.5625rem;
  }
}
.input-wrapper--active {
  box-shadow: 0px 0px 4px var(--Gray4);
}

.form__input {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  padding-left: 1.1875rem;
}
.form__input:focus {
  color: var(--Gray3);
}
.form__input:focus-visible {
  border: none;
  outline: none;
}

.btn {
  border-radius: 0.75rem;
  font-size: 0.875rem;
  line-height: 1.3125rem;
  width: 4.4375rem;
  height: 100%;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .btn {
    width: 5.875rem;
  }
}
.btn--medium {
  width: 7.25rem;
}
.btn--default {
  background-color: var(--Gray4);
  color: var(--Gray3);
  cursor: pointer;
}
.btn--default:hover {
  box-shadow: 2px 2px 10px var(--Gray4);
  background-color: var(--LightBlue);
  color: var(--White);
}
.btn--primary {
  background-color: var(--LightBlue);
  color: var(--White);
  cursor: pointer;
}
.btn--primary:hover {
  box-shadow: 2px 2px 10px var(--LightBlue);
}

.card-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 1024px) {
  .card-wrapper {
    flex-direction: row;
  }
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 26.375rem;
  width: 21.3125rem;
  padding: 3.0625rem 2.125rem 2.6875rem;
  position: relative;
  left: -2.125rem;
  cursor: pointer;
  border-radius: 1.5rem;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .card {
    left: 0;
  }
}
.card:hover {
  box-shadow: 0px 10px 30px 0px hsla(0deg, 0%, 20%, 0.1);
}
.card__icon {
  width: 4.1875rem;
  height: 4.1875rem;
  border-radius: 1rem;
  display: flex;
}
.card__icon .material-icons {
  margin: auto;
  width: 1.6875rem;
  height: 1.6875rem;
  color: var(--White);
}
.card__icon--edit {
  background-color: var(--LightBlue);
}
.card__icon--code {
  background-color: var(--LightGreen);
}
.card__icon--storage {
  background-color: var(--LightRed);
}
.card__title {
  width: 100%;
}
.card__footer {
  height: 2.625rem;
}

.footer {
  padding: 2.25rem 2.5625rem 1.5rem;
  background-color: var(--DarkBlue);
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
@media (min-width: 768px) {
  .footer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 2.875rem 8.5625rem 1.5rem 10.5rem;
  }
}
.footer ul {
  display: flex;
  flex-direction: column;
}
.footer ul li a {
  display: inline-block;
  font-size: 18px;
  font-weight: 400;
  line-height: 36px;
  color: var(--White);
  transition: transform 0.3s ease-in-out;
  position: relative;
}
.footer ul li a::before {
  content: "";
  border-left: 2px solid var(--White);
  height: 80%;
  position: absolute;
  left: -10px;
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s ease-in;
  transition-delay: 0.2s;
}
.footer ul li a:hover {
  transform: translateX(10px);
}
.footer ul li a:hover::before {
  transform: scaleY(1);
}
.footer__logo {
  font-family: Heebo;
  font-size: 36px;
  font-weight: 800;
  line-height: 53px;
  letter-spacing: 0em;
  text-align: left;
  color: var(--White);
}
.footer .social-wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 100px;
}
.footer .social-wrapper img {
  width: 1.5rem;
  height: 1.5rem;
}
.footer .form {
  margin-top: 0;
  margin-bottom: 9.625rem;
}
@media (min-width: 768px) {
  .footer .attribution {
    grid-column: 1/span 3;
    width: 100%;
    justify-self: center;
  }
}

.attribution p,
.attribution a {
  font-family: var(--font-montserrat);
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
  color: var(--White);
  text-align: center;
}
.attribution a {
  display: inline-block;
  font-weight: 500;
  transition: all 0.3s ease-in-out;
}
.attribution a::after {
  content: "";
  display: block;
  width: 100%;
  border-bottom: solid 2px var(--White);
  position: relative;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
  transition-delay: 0.2s;
}
.attribution a:hover {
  transform: translateY(-5px);
}
.attribution a:hover::after {
  transform: scaleX(1);
}

.anim,
.anim-left,
.anim-right {
  opacity: 0;
}

.works__gallery .img-wrapper:nth-child(odd) {
  animation: anim-left 1s forwards ease-in-out;
}
.works__gallery .img-wrapper:nth-child(even) {
  animation: anim-right 1s forwards ease-in-out;
}

@keyframes anim1 {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes anim-left {
  from {
    transform: translateX(-100px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes anim-right {
  from {
    transform: translateX(100px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}